@import url(https://fonts.googleapis.com/css?family=Roboto:400,500,700,900&display=swap&subset=cyrillic);
body {
  margin: 0;
  font-family: -apple-system, BlinkMacSystemFont, 'Segoe UI', 'Roboto', 'Oxygen',
    'Ubuntu', 'Cantarell', 'Fira Sans', 'Droid Sans', 'Helvetica Neue',
    sans-serif;
  -webkit-font-smoothing: antialiased;
  -moz-osx-font-smoothing: grayscale;
}

code {
  font-family: source-code-pro, Menlo, Monaco, Consolas, 'Courier New',
    monospace;
}

.week-days{display:grid;grid-template-columns:repeat(7, 1fr);text-align:start;background-color:#fff;height:5.5%;width:100%}.week-days:nth-child(1){border-top-left-radius:.4rem}

.week-day{display:flex;align-items:center;border:solid 1px;border-top:none;border-left:none;border-color:#e6e6e6;padding-left:18px;font-size:1.5rem;line-height:1.5;font-weight:bold;height:100%;background-color:aliceblue}.weekend{color:#f33}

.day-todo{background-color:bisque;margin-bottom:.2rem;padding:0 1rem;cursor:default;white-space:nowrap;overflow:hidden;text-overflow:ellipsis;width:80%;border-radius:4rem}

.date{position:relative;border:solid;border-width:1px 1px 0 0;box-sizing:border-box;border-color:#e6e6e6;height:100%;display:flex;justify-content:space-around;align-items:center;overflow:hidden;-webkit-transition:.4s;transition:.4s}.date-selected{background-color:aliceblue}.date-num{position:absolute;right:.5rem;top:.5rem;font-weight:bold;text-align:center}.date-num-current{background-color:#f33;color:#fff;border-radius:50%;padding:.2rem .4rem .2rem .3rem}.date-num-prev,.date-num-next{color:darkgrey;font-weight:normal}.date-wrap__tasks{margin-top:1.8rem;width:100%;display:flex;flex-direction:column;align-items:center}

.dates{display:grid;grid-template-columns:repeat(7, 1fr);height:94.5%;text-align:center;background-color:#fff;grid-template-rows:repeat(6, 1fr);border:none}

.calendar{width:100%;height:100%;box-shadow:-2px 0px 20px 0px rgba(50,50,50,0.32)}

.header{display:flex;justify-content:flex-start;align-items:center;flex:0 0 auto;font-size:1.5rem;height:5rem}.header__logo{width:35.5rem;padding-left:2.5rem;font-weight:bold}.header__info{display:flex;justify-content:flex-start;align-items:flex-end;width:100%}.header__info .prev-btn{margin-right:3rem;font-size:2rem;cursor:pointer;color:darkgrey;-webkit-transition:.4s;transition:.4s}.header__info .prev-btn:hover{color:#f33}.header__info .prev-btn:active{-webkit-transform:scale(0.9);transform:scale(0.9)}.header__info .next-btn{font-size:2rem;cursor:pointer;color:darkgrey;-webkit-transition:.4s;transition:.4s}.header__info .next-btn:hover{color:#f33}.header__info .next-btn:active{-webkit-transform:scale(0.9);transform:scale(0.9)}.header__month-year-wrap{text-align:center;margin-right:3rem;width:10rem;font-weight:bold}.header__month{margin-right:.5rem}

.todo-item{font-size:1rem;line-height:1.5;display:flex;justify-content:flex-start;align-items:center;margin-bottom:8px;background-color:#fff;cursor:pointer}.todo-item .text{white-space:nowrap;overflow:hidden;text-overflow:ellipsis;flex-grow:1}.todo-item .mark{margin-right:1rem;color:#24577B;opacity:.4;cursor:pointer;-webkit-transition:.4s;transition:.4s}.todo-item .mark:hover{-webkit-transform:scale(1.1);transform:scale(1.1)}.todo-item .mark:active{-webkit-transform:scale(0.9);transform:scale(0.9)}.todo-item .mark-complete{display:block;color:#7EB12C;margin-right:1rem;border:none;cursor:pointer;opacity:.3;-webkit-transition:.4s;transition:.4s}.todo-item .mark-complete:hover{-webkit-transform:scale(1.1);transform:scale(1.1)}.todo-item .mark-complete:active{-webkit-transform:scale(0.9);transform:scale(0.9)}.todo-item .delete{opacity:.3;margin-left:auto;color:#f33;cursor:pointer;-webkit-transition:.4s;transition:.4s}.todo-item .delete:hover{-webkit-transform:scale(1.1);transform:scale(1.1)}.todo-item .delete:active{-webkit-transform:scale(0.9);transform:scale(0.9)}.todo-item .completed{text-decoration:line-through;opacity:.3;-webkit-transition:.4s;transition:.4s}.todo-item:hover .delete,.todo-item:hover .mark-complete,.todo-item:hover .completed,.todo-item:hover .mark{opacity:1;-webkit-transition:.4s;transition:.4s}

.todo-list-wrap{width:100%;height:23.58rem;padding-right:.5rem;overflow-y:auto;overflow-x:none}.todo-list{list-style:none;padding:0}.item-enter{opacity:0}.item-enter-active{opacity:1;-webkit-transition:opacity 300ms ease-in;transition:opacity 300ms ease-in}.item-exit{opacity:1}.item-exit-active{opacity:0;-webkit-transition:opacity 50ms ease-in;transition:opacity 50ms ease-in}.empty{font-size:1.2rem;line-height:1.5;color:#a3a3a3}.hide{display:none}::-webkit-scrollbar{width:.2rem}::-webkit-scrollbar-track{box-shadow:inset 0 0 6px rgba(85,83,83,0.3);border-radius:.4rem}::-webkit-scrollbar-thumb{border-radius:.4rem;box-shadow:inset 0 0 6px rgba(100,97,97,0.5)}

.filter-todo{display:flex;justify-content:space-between;flex-direction:column;align-items:flex-start;font-size:1rem}.filter-todo .btn-group .filter-btn{font-size:1rem;border:none;background:none;padding:.3rem;cursor:pointer}.filter-todo .btn-group .active{position:relative}.filter-todo .btn-group .active:before{position:absolute;content:'';background-color:#0A64A4;left:0;bottom:0;width:100%;height:.125rem}.filter-todo .amount{padding:.3rem}@media (max-width: 800px){.footer{flex-direction:column;justify-content:center;align-items:center}.footer .btn-group{width:100%;justify-content:center}.footer .amount{margin-top:1rem}}

.todos{display:flex;flex-direction:column;justify-content:space-between;align-items:flex-start;height:100%;position:relative}.title{width:100%;font-size:1.2rem;line-height:1.5;font-weight:bold;text-align:center;color:#272525}.todos-add-btn{position:absolute;right:0;bottom:0;font-size:2rem;color:#7EB12C;cursor:pointer;-webkit-transition:-webkit-transform .4s;transition:-webkit-transform .4s;transition:transform .4s;transition:transform .4s, -webkit-transform .4s}.todos-add-btn:hover{-webkit-transform:scale(1.1);transform:scale(1.1)}.todos-add-btn:active{-webkit-transform:scale(0.9);transform:scale(0.9)}.todos-no-tasks{margin-bottom:50%;margin-left:1rem;color:#a3a3a3;font-size:1.2rem;text-align:center}

.form-wrap{display:flex;flex-direction:column;justify-content:space-between;align-items:center;height:100%;width:100%}.form-wrap .form-add{display:flex;flex-direction:column;width:100%;height:100%;justify-content:space-between;margin-bottom:1rem}.form-wrap .form-add .form__name{font-size:1.2rem;line-height:1.5}.form-wrap .form-add .form__title,.form-wrap .form-add .form__description{width:100%;text-align:left;font-size:1.2rem;border:0.1rem solid #60b2fa;border-radius:.4rem;padding:.2rem;-webkit-transition:.4s;transition:.4s}.form-wrap .form-add .form__title{border:none;border-radius:0;border-bottom:0.1rem solid #60b2fa}.form-wrap .form-add .form__title:focus{border-width:.2rem;-webkit-transition:.4s;transition:.4s}.form-wrap .form-add .form__description{height:47%;resize:none}.form-wrap .form-add .form__description:focus{border-width:.2rem;-webkit-transition:.4s;transition:.4s}.form-wrap .button-wrap{width:100%;display:flex;justify-content:space-around;align-items:center;font-size:2rem}.form-wrap .button-wrap .save{color:#7EB12C;cursor:pointer;-webkit-transition:-webkit-transform .4s;transition:-webkit-transform .4s;transition:transform .4s;transition:transform .4s, -webkit-transform .4s}.form-wrap .button-wrap .save:hover{-webkit-transform:scale(1.1);transform:scale(1.1)}.form-wrap .button-wrap .save:active{-webkit-transform:scale(0.9);transform:scale(0.9)}.form-wrap .button-wrap .cancel{cursor:pointer;color:#f33;-webkit-transition:-webkit-transform .4s;transition:-webkit-transform .4s;transition:transform .4s;transition:transform .4s, -webkit-transform .4s}.form-wrap .button-wrap .cancel:hover{-webkit-transform:scale(1.1);transform:scale(1.1)}.form-wrap .button-wrap .cancel:active{-webkit-transform:scale(0.9);transform:scale(0.9)}


.editbox__wrap{display:flex;flex-direction:column;justify-content:space-between;align-items:center;height:100%;width:100%}.editbox__task-wrap{display:flex;flex-direction:column;justify-content:space-between;align-items:center;height:100%;width:100%;position:relative;margin-bottom:1rem}.editbox__task__name{font-size:1.2rem;line-height:1.5;font-weight:bold}.editbox__task__title,.editbox__task__description{width:100%;font-size:1.2rem}.editbox__task__title{word-wrap:break-word}.editbox__task__description{height:47%;word-wrap:break-word;overflow-y:auto}.editbox__task__description-empty{color:#a3a3a3}.editbox__edit-btn{font-size:2rem;cursor:pointer;color:#f33;-webkit-transition:-webkit-transform .4s;transition:-webkit-transform .4s;transition:transform .4s;transition:transform .4s, -webkit-transform .4s}.editbox__edit-btn:hover{-webkit-transform:scale(1.1);transform:scale(1.1)}.editbox__edit-btn:active{-webkit-transform:scale(0.9);transform:scale(0.9)}



.editbox{text-align:center;height:100%}.editbox__task{display:flex;flex-direction:column;justify-content:space-between;align-items:center;height:100%}.editbox .editbox-no-tasks{font-size:1.2rem;line-height:1.5;color:#a3a3a3}

.social{display:block;width:2.5rem;height:2.5rem;margin:0.5rem}.social-img{width:100%;height:100%}

.socials{display:flex;justify-content:center;align-items:center}

.footer{width:100%;height:15%;background-color:#fff;border-radius:.4rem .4rem 0 0;display:flex;flex-direction:column;justify-content:center;align-items:center;box-shadow:0 10px 28px rgba(0,0,0,0.25),0 5px 10px rgba(0,0,0,0.22)}

.organizer-section{width:23rem;height:40%;background-color:#fff;border-radius:.4rem;padding:1rem;box-shadow:0 10px 28px rgba(0,0,0,0.25),0 5px 10px rgba(0,0,0,0.22)}

.organizer{width:35rem;height:100%;display:flex;flex-direction:column;justify-content:space-between;padding:0 2.5rem;-webkit-transition:.4s;transition:.4s;position:relative}.organizer-btn{position:absolute;right:3rem;bottom:5rem;font-size:2rem;z-index:10;-webkit-transition:.4s;transition:.4s;color:darkgrey;cursor:pointer;transition:.4s}.organizer-btn:hover{color:#f33;-webkit-transform:scale(1.1);transform:scale(1.1)}.organizer-btn:active{-webkit-transform:scale(0.9);transform:scale(0.9)}.organizer-btn-show{right:-3rem;-webkit-transition:.4s;transition:.4s}.hide{-webkit-transition:.4s;transition:.4s;margin-left:-35rem}

*{box-sizing:border-box;margin:0;padding:0;font-family:"Roboto",sans-serif}html,body{font-size:.83vw;height:100%;background-color:#f6f6f6;color:#272525}:active,:hover,:focus{outline:0;outline-offset:0}.app-wrapper{display:flex;flex-direction:column;min-height:100vh}.calendar-wrap{display:flex;justify-content:space-between;align-items:flex-start;flex:1 0 auto;height:calc(100vh - 5rem)}

